'use client';

import Image from "next/image";
import { useState, useEffect, useCallback } from "react";
import { fetchProdukWithFoto } from "@/app/lib/data";
import { alegreya, nosifer } from "@/app/ui/fonts";
import Navbar from "@/app/ui/navbar";

export default function MenuPage() {
  const [produkList, setProdukList] = useState([]);
  const [selectedProduk, setSelectedProduk] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [quantity, setQuantity] = useState(1);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const loadProduk = async () => {
      try {
        setLoading(true);
        const data = await fetchProdukWithFoto();
        setProdukList(data);
        setError(null);
      } catch (err) {
        console.error('Error fetching products:', err);
        setError('Gagal memuat produk. Silakan coba lagi.');
      } finally {
        setLoading(false);
      }
    };
    loadProduk();
  }, []);

  const openModal = useCallback((produk) => {
    setSelectedProduk(produk);
    setIsModalOpen(true);
    setQuantity(1);
    // Prevent body scroll when modal is open
    document.body.style.overflow = 'hidden';
  }, []);

  const closeModal = useCallback(() => {
    setIsModalOpen(false);
    setSelectedProduk(null);
    setQuantity(1);
    // Restore body scroll
    document.body.style.overflow = 'unset';
  }, []);

  // Handle escape key to close modal
  useEffect(() => {
    const handleEscape = (event) => {
      if (event.key === 'Escape' && isModalOpen) {
        closeModal();
      }
    };

    if (isModalOpen) {
      document.addEventListener('keydown', handleEscape);
      return () => document.removeEventListener('keydown', handleEscape);
    }
  }, [isModalOpen, closeModal]);

  // Handle click outside modal to close
  const handleModalOverlayClick = useCallback((e) => {
    if (e.target === e.currentTarget) {
      closeModal();
    }
  }, [closeModal]);

  const incrementQuantity = useCallback(() => {
    setQuantity(prev => Math.min(prev + 1, 99));
  }, []);

  const decrementQuantity = useCallback(() => {
    setQuantity(prev => Math.max(prev - 1, 1));
  }, []);

  const handleAddToCart = useCallback(() => {
    // Add cart logic here
    console.log(`Adding ${quantity}x ${selectedProduk?.nama_produk} to cart`);
    closeModal();
  }, [quantity, selectedProduk, closeModal]);

  const handleOrder = useCallback((produk) => {
    // Add order logic here
    console.log(`Ordering ${produk.nama_produk}`);
  }, []);

  if (loading) {
    return (
      <main className="min-h-screen bg-gradient-to-r from-red-950 to-black text-white flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#F8E4BE] mx-auto mb-4"></div>
          <p className={`${alegreya.className} text-xl`}>Memuat menu...</p>
        </div>
      </main>
    );
  }

  if (error) {
    return (
      <main className="min-h-screen bg-gradient-to-r from-red-950 to-black text-white">
        <Navbar />
        <div className="flex items-center justify-center py-20">
          <div className="text-center">
            <p className={`${alegreya.className} text-xl text-red-300 mb-4`}>{error}</p>
            <button 
              onClick={() => window.location.reload()}
              className="bg-[#9b684c] text-white py-2 px-6 rounded-full hover:bg-[#7d543d] transition-colors"
            >
              Coba Lagi
            </button>
          </div>
        </div>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-gradient-to-r from-red-950 to-black text-white">
      <Navbar />
      
      <section className="text-center py-20 px-4">
        <h1 className={`${nosifer.className} text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-extrabold tracking-wide text-[#F8E4BE] mb-6`}>
          MENU
        </h1>
        
        <p className={`${alegreya.className} text-lg sm:text-xl md:text-2xl text-[#f5deb3]/80 mb-12 max-w-4xl mx-auto`}>
          Jelajahi semua rasa kopi bersama kami. Selalu ada secangkir kopi baru
          yang layak dicoba.
        </p>

        {produkList.length === 0 ? (
          <p className={`${alegreya.className} text-xl text-[#f5deb3]/60`}>
            Belum ada produk tersedia.
          </p>
        ) : (
          <div className="grid gap-6 sm:gap-8 md:gap-10 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 max-w-7xl mx-auto px-4">
            {produkList.map((produk) => (
              <article
                key={produk.id_produk}
                className="bg-[#fcefdc] rounded-xl shadow-lg overflow-hidden text-center hover:shadow-2xl transition-all duration-300 transform hover:-translate-y-2 focus-within:ring-2 focus-within:ring-[#F8E4BE]"
              >
                <div className="relative w-full h-64 sm:h-72 md:h-80 bg-gray-100">
                  <Image
                    src={produk.foto}
                    alt={`Foto produk ${produk.nama_produk}`}
                    fill
                    className="object-cover cursor-pointer transition-transform duration-300 hover:scale-105"
                    onClick={() => openModal(produk)}
                    sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 25vw"
                    onError={(e) => {
                      e.target.src = '/placeholder-coffee.jpg'; // Add fallback image
                    }}
                  />
                </div>
                
                <div className="p-6 sm:p-8">
                  <h2 className="text-xl sm:text-2xl font-semibold text-[#300000] mb-3 line-clamp-2">
                    {produk.nama_produk}
                  </h2>
                  
                  <p className="text-sm sm:text-base text-[#300000]/70 mb-4">
                    Coffee 50% | Milk 50%
                  </p>
                  
                  <p className="text-lg sm:text-xl font-bold text-[#300000] mb-6">
                    Rp. {produk.harga_produk?.toLocaleString() || '0'}
                  </p>
                  
                  <div className="flex flex-col sm:flex-row gap-3">
                    <button 
                      onClick={() => openModal(produk)}
                      className="bg-[#6b5b95] text-white py-3 px-4 sm:px-6 rounded-full hover:bg-[#5a4a7a] focus:bg-[#5a4a7a] focus:outline-none focus:ring-2 focus:ring-[#6b5b95] focus:ring-offset-2 transition-colors duration-200 font-medium text-sm sm:text-lg flex-1"
                      aria-label={`Lihat detail ${produk.nama_produk}`}
                    >
                      DETAIL
                    </button>
                    <button 
                      onClick={() => handleOrder(produk)}
                      className="bg-[#9b684c] text-white py-3 px-4 sm:px-6 rounded-full hover:bg-[#7d543d] focus:bg-[#7d543d] focus:outline-none focus:ring-2 focus:ring-[#9b684c] focus:ring-offset-2 transition-colors duration-200 font-medium text-sm sm:text-lg flex-1"
                      aria-label={`Pesan ${produk.nama_produk}`}
                    >
                      ORDER
                    </button>
                  </div>
                </div>
              </article>
            ))}
          </div>
        )}
      </section>

      {/* Modal Detail Produk */}
      {isModalOpen && selectedProduk && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
          onClick={handleModalOverlayClick}
          role="dialog"
          aria-modal="true"
          aria-labelledby="modal-title"
        >
          <div className="bg-[#fcefdc] rounded-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="relative">
              <button
                onClick={closeModal}
                className="absolute top-4 right-4 bg-red-500 text-white rounded-full w-10 h-10 flex items-center justify-center hover:bg-red-600 focus:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 transition-colors z-10"
                aria-label="Tutup modal"
              >
                <span aria-hidden="true">âœ•</span>
              </button>
              
              <div className="relative w-full h-64 sm:h-80 md:h-96 bg-gray-100">
                <Image
                  src={selectedProduk.foto}
                  alt={`Foto produk ${selectedProduk.nama_produk}`}
                  fill
                  className="object-cover rounded-t-2xl"
                  sizes="(max-width: 768px) 100vw, 672px"
                  onError={(e) => {
                    e.target.src = '/placeholder-coffee.jpg';
                  }}
                />
              </div>
              
              <div className="p-6 sm:p-8">
                <h2 
                  id="modal-title"
                  className={`${nosifer.className} text-2xl sm:text-3xl md:text-4xl font-bold text-[#300000] mb-4`}
                >
                  {selectedProduk.nama_produk}
                </h2>
                
                <div className="grid md:grid-cols-2 gap-6 mb-6">
                  <div>
                    <h3 className="text-lg sm:text-xl font-semibold text-[#300000] mb-2">
                      Informasi Produk
                    </h3>
                    <div className="space-y-2 text-sm sm:text-base text-[#300000]/80">
                      <p><span className="font-medium">Kategori:</span> Kopi Premium</p>
                      <p><span className="font-medium">Komposisi:</span> Coffee 50% | Milk 50%</p>
                      <p><span className="font-medium">Ukuran:</span> Regular (350ml)</p>
                      <p><span className="font-medium">Suhu:</span> Panas / Dingin</p>
                    </div>
                  </div>
                  
                  <div>
                    <h3 className="text-lg sm:text-xl font-semibold text-[#300000] mb-2">
                      Deskripsi
                    </h3>
                    <p className="text-[#300000]/80 text-sm leading-relaxed">
                      Nikmati cita rasa kopi premium yang dipadukan dengan sempurna. 
                      Setiap tegukan memberikan pengalaman yang tak terlupakan dengan 
                      aroma yang menggugah selera dan rasa yang kaya.
                    </p>
                  </div>
                </div>
                
                <div className="border-t border-[#300000]/20 pt-6">
                  <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 mb-6">
                    <div>
                      <p className="text-sm text-[#300000]/60 mb-1">Harga</p>
                      <p className="text-2xl sm:text-3xl font-bold text-[#300000]">
                        Rp. {selectedProduk.harga_produk?.toLocaleString() || '0'}
                      </p>
                    </div>
                    
                    <div className="flex items-center gap-3">
                      <button 
                        onClick={decrementQuantity}
                        disabled={quantity <= 1}
                        className="bg-gray-200 text-[#300000] px-4 py-2 rounded-full hover:bg-gray-300 focus:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-400 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        aria-label="Kurangi jumlah"
                      >
                        -
                      </button>
                      <span className="text-xl font-medium text-[#300000] px-4 min-w-[3rem] text-center">
                        {quantity}
                      </span>
                      <button 
                        onClick={incrementQuantity}
                        disabled={quantity >= 99}
                        className="bg-gray-200 text-[#300000] px-4 py-2 rounded-full hover:bg-gray-300 focus:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-400 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        aria-label="Tambah jumlah"
                      >
                        +
                      </button>
                    </div>
                  </div>
                  
                  <div className="flex flex-col sm:flex-row gap-4">
                    <button 
                      onClick={closeModal}
                      className="bg-gray-400 text-white py-3 px-6 rounded-full hover:bg-gray-500 focus:bg-gray-500 focus:outline-none focus:ring-2 focus:ring-gray-400 focus:ring-offset-2 transition-colors duration-200 font-medium flex-1"
                    >
                      TUTUP
                    </button>
                    <button 
                      onClick={handleAddToCart}
                      className="bg-[#9b684c] text-white py-3 px-6 rounded-full hover:bg-[#7d543d] focus:bg-[#7d543d] focus:outline-none focus:ring-2 focus:ring-[#9b684c] focus:ring-offset-2 transition-colors duration-200 font-medium sm:flex-[2]"
                    >
                      TAMBAH KE KERANJANG
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </main>
  );
}